.Topic {
  text-align: center;
  background-color: black;
  padding: 60px;
  color: white;
}
.Topic h1 {
  font-weight: 600;
  font-size: 50px;
  margin-bottom: 9px;
  /* color: white; */
}
.Topic p {
  font-size: 20px;
  margin-top: 4px;
}
.cardlist {
  display: flex;
  flex-wrap: wrap;   
}
.card {
  width: 270px;
  height: 350px;
  border: 1px #ddd;
  /* justify-content: space-around; */
  margin: 100px 15px 10px 70px;
  box-shadow: 0 5px 9px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
  border-radius: 9px;
}
.poster {
  width: 270px;
  height: 170px;
  /* border-radius: 8px; */
  border-top-right-radius: 9px;
  border-top-left-radius: 9px;
}
.spec {
  text-align: center;
}
.sale-badge {
  position: absolute;
  background-color: black;
  color: white;
  border-radius: 6px;
  padding: 3px 7px;
  font-size: 12px;
  margin-left: 226px;
  margin-top: 8px;
  font-weight: bold;
  /* top: 10px; */
  /* left: 10px; */
  /* background-color: black; */
  /* color: white; */
  /* padding: 5px 10px; */
  /* border-radius: 3px; */
  /* font-size: 12px; */
  /* font-weight: bold; */
}
.button {
  color: black !important;
  border-color: black !important;
}
.button:hover {
  background-color: black !important;
  color: white !important;
}
.footer {
  background-color: black;
  color: white;
  padding: 50px;
  margin-top: 90px;
  text-align: center;
}
.num {
  background-color: black;
  color: white;
  border-radius: 6px;
  
}